<script setup>

import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import {onMounted, ref} from 'vue';
import {useRoute} from 'vue-router';
import http from "../services/api/axios";
import {Icon} from "@iconify/vue";
import PageTitleWithGoBackButton from "../components/LayoutItems/PageTitleWithGoBackButton";
import useErrorAlert from "../composables/useErrorAlert";

const route = useRoute();
const teenagerInfo = ref(null)

const loading = ref(true)

const {alertThisError} = useErrorAlert()

onMounted(async () => {
  await fetchTeenager()
})

const fetchTeenager = async () => {
  try {
    loading.value = true
    teenagerInfo.value = (await http.get(`/api/juvenile/juveniles/${route.params.id}/is_out_detail/`)).data
    loading.value = false
  } catch (e) {
    loading.value = false
    alertThisError(e)
  }
}

